.svp_player {
  max-width: 100%;
  .plyr__poster {
    background-size: cover;
  }
  a.plyr__controls__item {
    color: #fff;
  }
  .plyr:fullscreen {
    z-index: 99999;
  }
}
.svp_playlist {
  .default {
    background: #1a1a1a;
    .svp_item {
      padding: 20px;
      padding: 10px 10px;
      min-height: 60px;
      display: flex;
      flex-direction: column;
      justify-content: end;
      cursor: pointer;
      &.active {
        background: #1aaefe;
      }
      .playing,
      .next {
        color: #fff;
        margin-bottom: 5px;
        font-size: 16px;
      }
    }
    .title {
      color: #fff;
      margin-bottom: 5px;
      font-size: 20px;
      margin-top: 5px;
    }
  }
}
